<template>
  <div class="person-message">
    <div class="message">
      <div class="img">
        <el-upload class="avatar-uploader" action="/api/avatar" :headers = "imgheader" :show-file-list="false" :on-success="handleAvatarSuccess" :before-upload="beforeAvatarUpload">
        <img v-if="imageUrl" :src="imageUrl" class="avatar">
        <i v-else class="el-icon-plus avatar-uploader-icon"></i>
      </el-upload>
      </div>

      <p v-if="userInfo.name">{{userInfo.name}}</p>
      <p v-else></p>
      <p>:{{userInfo.money}}</p>
      <a @click="changeName" href="javascript:;"><span></span></a>
    </div>
    <p><span></span>{{userInfo.phone}}</p>
    <p><span></span>{{userInfo.updated_at}}</p>
  </div>
</template>

<script>
import Axios from "@/common/js/Axios";
export default {
  mounted() {
    this.getUserInfo();
  },
  data() {
    return {
      token: localStorage.getItem("Authorization"),
      //
      userInfo: {},
      imageUrl: "",
      //
      imgheader : {
        "Authorization" : "Bearer "+localStorage.getItem("Authorization")
      },
    };
  },
  methods: {
    //
    getUserInfo() {
      Axios.getAxios(
        "/api/user/",
        {},
        {
          Authorization: "Bearer " + this.token
        }
      ).then(
        res => {
          if (res) {
            this.userInfo = res.data.data;
            this.imageUrl = res.data.data.image;
          } else {
            this.$message.error("");
          }
        },
        err => {
          this.$message("");
        }
      );
    },
    //

    changeTx() {},
    //
    handleAvatarSuccess(res, file) {
      this.imageUrl = URL.createObjectURL(file.raw);
      this.$message("");
    },
    //
    beforeAvatarUpload(file) {
      const isJPG = file.type === "image/jpeg";
      const isPNG = file.type === "image/png";
      const isLt2M = file.size / 1024 / 1024 < 2;
      if (!isJPG && !isPNG) {
        this.$message.error(" JPGPNG !");
        return false;
      }
      if (!isLt2M) {
        this.$message.error(" 2MB!");
        return false;
      }
      console.log(file);
      this.file = file;
      return (isJPG || isPNG) && isLt2M;
    },
    //
    changeName() {
      this.$prompt("", "", {
        confirmButtonText: "",
        cancelButtonText: ""
      })
        .then(({ value }) => {
          Axios.postAxios(
            "/api/changeuserdata",
            {
              type: "name",
              data: value
            },
            {
              Authorization: "Bearer " + this.token
            }
          ).then(
            res => {
              this.$message.success("");
              this.getUserInfo();
            },
            err => {
              this.$message.error("");
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    }
  }
};
</script>

<style lang="stylus" scoped>
.img{
    width: 80px;
    height: 80px;
    border-radius: 50%;
    margin: 0 auto;
    img{
      width 100%;
      border-radius:10px;
    }
}
</style>




