<template>
  <span>
    <div class="company-container">
      <div class="company-introduction">
        <div class="company-profile">
          <h3></h3>
          <span>
            +++--
          </span>
          <span>
            +
          </span>
        </div>
        <div class="server-us clear">
          <h3></h3>
          <ul class="server-list">
            <li>
              <p></p>
              <span>
                 
              </span>
            </li>
            <li>
              <p></p>
              <span>
                
              </span>
            </li>
            <li>
              <p></p>
              <span>
                7*24 
              </span>
            </li>
            <li>
              <p></p>
              <span>
                 
              </span>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <!---->
    <div :style="'background-image:url('+br2+')'" class="skill-container">
      <div class="skill-solve">
        <h3></h3>
        <p></p>
        <div>
          <img :src="skillImg">
        </div>
      </div>
    </div>
    <!---->
    <div class="development-container">
      <div class="development-history">
        <h3></h3>
        <span>
            
        </span>
      </div>
    </div>
  </span>
</template>

<script>
export default {
    data(){
        return{
            // image
            br2 : require('@/assets/forme/images/br2.png'),
            skillImg:require('@/assets/forme/images/skill-img.png')
            // image
        }
    }
};
</script>

<style lang="stylus" scoped>
/*  */
.skill-container {
  width: 100%;
  min-width: 1200px;
  height: 505px;
}
.skill-solve {
  width: 1200px;
  margin: auto;
  text-align: center;
}
.skill-solve h3, .development-history h3 {
  color: #c5965a;
  font-size: 30px;
  font-weight: normal;
  padding: 25px 0 30px 0;
  text-align: center;
}
.skill-solve p {
  color: #fff;
  margin-bottom: 45px;
}
/*  */
.development-container {
  width: 100%;
  min-width: 1200px;
}
.development-history {
  width: 1200px;
  margin: 20px auto 60px auto;
}
.development-history span {
  display: block;
  text-indent: 2em;
  font-size: 16px;
  color: #666;
  line-height: 30px;
}
</style>




