<template>
  <div class="t-sell" avalonctrl="vmPos">
    <table cellspacing="0" class="list-wrap " style="font-size: 14px">
      <tbody>
        <tr>
          <th style="width: 280px" class="pl10"></th>
          <th></th>
          <th style="width: 80px"></th>
          <th style="width: 80px"></th>
          <th style="width: 80px"></th>
          <th style="width: 80px;"></th>
          <th style="width: 120px"></th>
        </tr>

        <tr v-for="(item,index) in nowShare" :key="index">
          <td class="pl10">{{item.created_at}}<br>
            <!-- <span>ID</span> <span class="txt-up">20181203000038</span> -->
          </td>
          <td>
            <span class="txt-up" style="display: none;">......</span>
            <span class="txt-up" style="display: none;">......</span>
            <br style="display: none;">
            {{item.name}}{{item.code}} <span class="txt-up">{{item.num}} </span><br>

            {{item.count_xingyongjin}}                         
          </td>
          <td>
            <span class="currprice ">{{item.price}}</span>
            <span class="currprice " style="display: none;">------</span>
          </td>
          <td>
            <span class="currprice ">{{item.current_price}}</span>
            <span class="driftprice " style="display: none;">------</span>
          </td>
          <td>
            <span class="driftprice txt-up">{{item.float_earn}}</span>
            <span class="driftprice " style="display: none;">------</span>
          </td>
          <td>
            <span class="driftprice ">{{item.multiple}}</span>
            <span class="driftprice " style="display: none;">------</span>
          </td>
          <td>
            <span class="txt-up" style="display: none;">......</span>
            <span class="txt-up" style="display: none;">......</span>

            <button class="btn" @click="sell(item.tactic_id)" title=""></button>
            <button @click="add(item.tactic_id)" class="btn" style="margin-top: 5px;"></button>
          </td>
        </tr>

      </tbody>
    </table>
    <div style="padding-top: 150px; text-align: center; display: none;">
      <p class="pb20 ">A</p>
      <p>
        <button style="display: block; width: 200px; height: 40px; text-align: center; color: #fff; border: none; background: url(../newImages/btn-bg.png) no-repeat!important; border-radius: 20px; margin: 0 auto;"></button>
      </p>
    </div>
    <div class="pagination clearfix " style="">
      <ul class="clearfix " style="margin-right: 0">
        <li>
          <a @click="fy(-1)" style="transform:scale(-1)" title="" class="page prev iconfont">&#xe611;</a>
        </li>

        <li style="line-height: 10px; height: 30px; vertical-align: middle"><i style="color: red">{{all}}</i> &nbsp;&nbsp; <i style="color: red">{{now}}</i>&nbsp;/&nbsp;{{last}}
        </li>
        <li>
          <a @click="fy(1)" title="" class="page next iconfont">&#xe611;</a>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Axios from "@/common/js/Axios";
export default {
  mounted() {
    this.getNow(this.now);
  },
  data() {
    return {
      token: localStorage.getItem("Authorization"),
      //
      nowShare: [],
      //
      all:1,
      //
      now:1,
      // 
      last:1
    };
  },
  methods: {
    //
    fy(v){      
      if(v == -1){
        this.getNow(--this.now);
      }else{
        this.getNow(++this.now);
      }
    },
    // 
    async add(id){
      let ok = true;
      let mvalue = 0;
      await this.$prompt('', '', {
          confirmButtonText: '',
          cancelButtonText: '',
        }).then(({ value }) => {
          mvalue = value
        }).catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });       
          ok = false;
      });
      if(!ok){
        return false;
      }
      Axios.postAxios(
        '/api/tactics/addshoudong',
        {
          tactic_id : id,
          xingyongjin : mvalue
        },
        {
          Authorization: "Bearer " + this.token
        }
      ).then(res=>{     
          console.log(res);
          if(!res.status){
            this.$message.error(res.data.msg);
          }else{            
            this.$message.success(res.data.msg);            
            this.getNow(this.now);
          }
      },err=>{
        console.log(err);
        this.$message.error('');
      })
    },
    //
    async sell(code) {
      let ok = true;
      await this.$confirm("?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {})
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
          ok = false;
        });
      if (!ok) {
        return false;
      }
      Axios.postAxios(
        "/api/tactics/sale",
        {
          tactic_id: code
        },
        {
          Authorization: "Bearer " + this.token
        }
      ).then(
        res => {
          if (res.data.status == false) {
            this.$message.error(res.data.msg);
          } else {
            this.$message.success(res.data.msg);
            this.getNow(this.now);
          }
        },
        err => {
          this.$message.error("");
        }
      );
    },
    //
    getNow(page) {
      if(page<1){        
        this.now = 1;
        return false;
      }
      if(page>this.last){
        this.now=  this.last;
        return false;
      }
      Axios.getAxios(
        "/api/strategyChiCangPc/normal",
        {
          page:page
        },
        {
          Authorization: "Bearer " + this.token
        }
      ).then(res => {
        console.log(res);
        this.nowShare = res.data.data.data;
        this.all = res.data.data.total;
        this.now = res.data.data.current_page;
        this.last = res.data.data.last_page;
      });
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/common/css/pager';
.list-wrap th {
  border: none;
  height: 40px;
  background-color: #E9E9E9;
  font-weight: bold;
  text-align: left;
  padding-left: 10px;
}
.list-wrap td {
  padding-top: 15px;
  padding-bottom: 15px;
  border-bottom: 1px solid #DBDBDB;
}
.iconfont {
  font-size: 20px !important;
  border: none !important;
  cursor: pointer;
}
</style>




