<template>
  <div class="login-wrap" avalonctrl="vmRegistration">
    <div class="login-list">
      <h2></h2>
      <div id="reg" class="form">
        <div>
          <label></label>
          <input type="number" v-model="tel" id="mobile" name="mobile">
          <span></span>
        </div>
        <div>
          <label></label>
          <input type="number" v-model="name" id="mobile2" name="mobile">
          <span></span>
        </div>
        <div>
          <label></label>
          <input type="password" v-model="password" id="password" name="password">
          <span>6-16</span>
        </div>
        <div>
          <label></label>
          <input type="password" v-model="passwordRepeat" id="passwordConfirm" name="passwordConfirm">
          <span></span>
        </div>
        <div>
          <label></label>
          <input type="text" id="agentID" v-model="code" name="agentID">
          <span></span>
        </div>
        <div class="input-yanz">
          <label></label>
          <input type="text" v-model="msg" name="vcode">
          <button @click="sendMsg" type="button">{{getYzm}}</button>
          <span>60</span>
        </div>
        <div class="form-group"></div>
        <p class="xieyi">
          <input type="checkbox" @click="changeAgree" :checked="isAgree">
          <a href="#" class="color00">{{fw}}</a>
        </p>
        <button @click="sendRegister" class="btn btn-zhuce" type="button" style="cursor: pointer"></button>
        <p class="login-d">
          <router-link to="/login/login" tag="a" class="color00"></router-link>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import Axios from "@/common/js/Axios";

export default {
  mounted(){
    this.getFw();
  },
  data() {
    return {
      name:"",
      fw: "",
      //
      verificationKey: "",
      //60s
      getYzm: "",
      canSend: true,
      //60s
      isAgree: true,
      tel: "",
      password: "",
      passwordRepeat: "",
      code: "",
      msg: ""
    };
  },
  beforeDestroy() {
    clearInterval(tmpInterval);
  },
  methods: {
    getFw() {
      Axios.getAxios("/api/contactWay", {}).then(
        res => {          
          this.fw = res.data.data.web_title;
          console.log(this.fw)
        },
        err => {
          console.log(err);
        }
      );
    },
    //
    changeAgree() {
      this.isAgree = !this.isAgree;
    },
    //
    sendRegister() {
      if (!this.isAgree) {
        this.$message.error("");
        return false;
      }
      Axios.postAxios("/api/users", {
        code: this.code,
        phone: this.tel,
        verificationKey: this.verificationKey,
        verificationCode: this.msg,
        password: this.password,
        passwordRepeat: this.passwordRepeat,
        name:this.name
      }).then(
        async res => {
          console.log(res);
          if (!res.data.status) {
            this.$message.error(res.data.msg);
          } else {
            await this.$message.success("");
            this.$router.push("/login/login");
          }
        },
        err => {
          console.log(err.response);
        }
      );
    },
    //
    sendMsg() {
      if (!this.canSend) {
        this.$message.error(`${this.getYzm}`);
        return false;
      }
      if (this.tel == "") {
        this.$message.error("");
        return false;
      }
      Axios.postAxios("/api/verificationCode/register", {
        phone: this.tel
      }).then(
        res => {
          if (!res.data.status) {
            this.$message.error(res.data.msg);
          } else {
            this.canSend = false;
            this.$message.success(res.data.msg);
            this.verificationKey = res.data.data.verificationKey;
            let time = 60;
            var tmpInterval = setInterval(() => {
              time--;
              this.getYzm = time;
              if (time == 0) {
                this.canSend = true;
                this.getYzm = "";
                clearInterval(tmpInterval);
              }
            }, 1000);
          }
        },
        err => {
          this.$message.error("");
        }
      );
    }
  }
};
</script>

<style lang="stylus" scoped>
@import '~@/assets/login/css/register';
#reg input {
  text-indent: 1em;
}
</style>





